import React from 'react';
import { Container, Col, Row, Button } from "react-bootstrap";
import ReactPlayer from 'react-player'
import './basis.css';

const styles = {
    homeContainer: {
        height: 1356,
               
        backgroundImage: `url(${"/testreact/images/2020-09-10 Eerste oefensessie overzicht.jfif"})`,
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover'
    },

    content: {
      height: '100%',
      width: '100%',
      backgroundColor: 'rgba(250, 250, 250, 0.8)',
    }
};

class Home extends React.Component {
  
  render () {
    return (
      <div style={styles.homeContainer}>
        <Container style={styles.content} fluid="md">
            <Row xs={1} md={1}  className="row">
             <Col xs={12} md={4} lg={5} className="col">        
                <h2>Silvermusic <span className="xSmall"> V2.1 </span> </h2>
              </Col>  
              <Col xs={12} md={8} lg={7} className="col">        
                <h7>Muzikale Verbinding in Zilverkamp Huissen </h7>
              </Col> 
            </Row>
          
            <Row xs={1} md={2} className="row">
              <Col xs={12} md={7} lg={8} className="col">        
               <ReactPlayer width="100%" controls= "true"  playing={true}  url='https://www.youtube.com/watch?v=yKQcVkCsoCM' />
               <Row className="rowTopPadding rowBottomPadding" >
                 
                <Col xs={9} md={9} lg={10} className="col">        
                  <h4 >Ben je geinteresseerd: neem dan contact op met Hans Vos via email: jhjmvos@gmail.com</h4>
                </Col>  
                <Col xs={3} md={3} lg={2} className="col">        
                  <img width="100%" src={process.env.PUBLIC_URL + '/images/2020-09-10 Hans on percussion.jfif'} alt="2020-09-10 Hans on percussion" />
                </Col>  
                    
                </Row>
              </Col>  

              <Col xs={12} md={5} lg={4} className="col">        
                <img width="100%" src={process.env.PUBLIC_URL + '/images/Kranten arrtikel Silvermusic okt 2020.jpg'} alt="Kranten arrtikel Silvermusic okt 2020.jpg" />
              </Col>  
            </Row>

            <Row xs={1} md={2} className="row">
                      
              <Col xs={12} md={6} lg={7} className="col">        
                <img width="100%" src={process.env.PUBLIC_URL + '/images/2020-09-10 Eerste oefensessie overzicht.jfif'} alt="Eerste oefensessie overzicht.jfif"/>
              </Col>
              <Col xs={12} md={6} lg={5} className="col">        
                <img width="100%" src={process.env.PUBLIC_URL + '/images/Jan op gitaar.png'} alt="Jan op gitaar.png"/>
              </Col>
            </Row>


        </Container>
      </div>
   )
  }
}

export default Home  