import React, {useState, useEffect} from 'react';
import { Modal, Table, Col, Row, Button} from "react-bootstrap";
import { FaEdit} from 'react-icons/fa';
import TestLocalStorage from './TestLocalStorage';

function LoginModalModal() {
    const [show, setShow] = useState(false);
    const [articleId, setArticleId] = useState(null);
    
    // const [id, setId] =  useState(1);
    // const [title, setTitle] =  useState("this song");
    const [response, setResponse] = useState(null);
    
    const [statusTekst, setStatusTekst] = useState("--");
    const code="10";
    const handleShow = () => setShow(true);

    // const updateUrl = "http://localhost/php_api_test/apiBasic/updateSongV2.php";
    const updateUrl = "https://silvermusic.nl/test/apiBasic/updateSongV2.php";


    const song =  {code} 

    const requestOptions = {
      method: 'POST',
      headers: {
        'Accept': 'application/json, text/plain, */*',
        'Content-Type': 'application/json'
      },
      body:  JSON.stringify(song)
    };
  

    const handleSave = () => {
      setStatusTekst("op Save geklikt")
      // POST request using axios inside useEffect React hook
      fetch(updateUrl, requestOptions)
      .then((res) => {
        if (!res.ok) { 
          throw res;  }
        return res.json()

        // .then(response => setResponse(res.data))
    })
      // empty dependency array means this effect will only run once (like componentDidMount in classes)
      // props.callBack();
    }

    const handleClose = () => { 
      // props.callBack();
      setShow(false); 
    };

    const handleSaveAndClose = () => { 
      handleSave();
      handleClose(); 
    };

    return (
      <>
        <Button variant="dark" onClick={handleShow}>
          <FaEdit size={30} style={{ color: 'white' }} />
           {" "}
         {/* // class="fas fa-pencil-alt fa-fw" */}
          <span className="small"> ToegangsCode </span> 
        </Button>
  
        <Modal show={show} onHide={handleClose} active="true" backdrop={false}>
          <Modal.Header closeButton>
            <Modal.Title>Toegangcode</Modal.Title>
          </Modal.Header>
          <Modal.Body> <TestLocalStorage/> (refresh browser na wijzigen)</Modal.Body>
          <Modal.Footer> {statusTekst} </Modal.Footer>
        </Modal>
      </>
    );
  } export default LoginModalModal; 